import { ChatInteractionContext } from "../useAgent.types"

function getPromptWithContext(prompt: string, context: Array<ChatInteractionContext> = []) {
  let finalPrompt = ""

  for (const contextItem of context) {
    switch (contextItem.type) {
      case "cite":
        finalPrompt += `
                > ${contextItem.text}
                `
        break
      default:
        continue
    }
  }

  finalPrompt += `${finalPrompt.length ? "\n --- \n" : ""}${prompt}`
  return finalPrompt
}

export { getPromptWithContext }
